import { useState, useEffect } from 'react';
import { ChevronDownIcon, XMarkIcon, UserIcon, BriefcaseIcon, BoltIcon, AcademicCapIcon, CheckBadgeIcon, PhoneIcon, EnvelopeIcon, IdentificationIcon } from '@heroicons/react/24/outline';

const JobSeekerRegistration = () => {
  // State management
  const [profileMenuOpen, setProfileMenuOpen] = useState(false);
  const [showTipsModal, setShowTipsModal] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  
  const [seekerForm, setSeekerForm] = useState({
    name: '',
    phone: '',
    countryCode: '+233',
    email: '',
    skills: '',
    experience: '',
    seekerDetails: [],
    goals: ''
  });

  // Mock user data
  const user = {
    name: 'John Doe',
    email: 'john@example.com',
    profileImage: null
  };

  const userInitials = user.name ? user.name.charAt(0).toUpperCase() : 'U';

  // Form options
  const seekerOptions = [
    { 
      value: 'AI Coach (Interview Prep)', 
      label: 'AI Interview Coach', 
      description: 'Practice mock interviews with personalized AI feedback and improvement tips',
      icon: AcademicCapIcon
    },
    { 
      value: 'Resume Builder', 
      label: 'Resume Builder', 
      description: 'Create and optimize your resume with AI-powered suggestions and templates',
      icon: IdentificationIcon
    },
    { 
      value: 'Career Navigator', 
      label: 'Career Navigator', 
      description: 'Get personalized career path guidance and industry insights',
      icon: BoltIcon
    },
    { 
      value: 'Confidence Booster', 
      label: 'Confidence Booster', 
      description: 'Build confidence through targeted coaching and skill development',
      icon: CheckBadgeIcon
    }
  ];

  // Country codes
  const countryCodes = [
    { code: '+233', country: 'Ghana' },
    { code: '+1', country: 'US/Canada' },
    { code: '+44', country: 'UK' },
    { code: '+234', country: 'Nigeria' }
  ];

  // Form validation
  const isFormValid = seekerForm.name.trim() !== '' &&
    seekerForm.email.trim() !== '' &&
    seekerForm.phone.trim() !== '' &&
    seekerForm.skills.trim() !== '' &&
    seekerForm.experience.trim() !== '' &&
    seekerForm.seekerDetails.length > 0;

  // Handle form input changes
  const handleInputChange = (field, value) => {
    setSeekerForm(prev => ({
      ...prev,
      [field]: value
    }));
  };

  // Handle checkbox changes
  const handleCheckboxChange = (value) => {
    setSeekerForm(prev => ({
      ...prev,
      seekerDetails: prev.seekerDetails.includes(value)
        ? prev.seekerDetails.filter(item => item !== value)
        : [...prev.seekerDetails, value]
    }));
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!isFormValid) {
      alert('Please fill in all required fields');
      return;
    }

    setIsSubmitting(true);
    
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      alert('Registration completed successfully! Welcome to QWESI AI.');
      
      // Reset form
      setSeekerForm({
        name: '',
        phone: '',
        countryCode: '+233',
        email: '',
        skills: '',
        experience: '',
        seekerDetails: [],
        goals: ''
      });
      
    } catch (error) {
      alert('Registration failed. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  // Handle click outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (!event.target.closest('.profile-dropdown')) {
        setProfileMenuOpen(false);
      }
    };

    document.addEventListener('click', handleClickOutside);
    return () => document.removeEventListener('click', handleClickOutside);
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      {/* Header */}
      <header className="bg-white/80 backdrop-blur-sm border-b border-slate-200/50">
        <div className="px-6 py-4">
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
            {/* Logo and Title */}
            <div className="flex items-center space-x-4">
              <div className="w-10 h-10 bg-gradient-to-br from-orange-500 to-red-500 rounded-xl flex items-center justify-center">
                <span className="text-white font-bold text-lg">Q</span>
              </div>
              <div>
                <h1 className="text-2xl font-black bg-gradient-to-r from-slate-800 to-orange-600 bg-clip-text text-transparent">
                  Job Seeker Registration
                </h1>
                <p className="text-sm text-slate-600 mt-1">
                  Get personalized career guidance and <span className="font-bold text-orange-600">job opportunities</span>
                </p>
              </div>
            </div>

            {/* Header Actions */}
            <div className="flex items-center space-x-3">
              {/* Tips Button */}
              <button
                onClick={() => setShowTipsModal(true)}
                className="flex items-center space-x-2 px-4 py-2 bg-slate-100 hover:bg-slate-200 rounded-lg transition-colors"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" 
                        d="M8.228 9c.549-1.165 2.03-2 3.772-2 2.21 0 4 1.343 4 3 0 1.4-1.278 2.575-3.006 2.907-.542.104-.994.54-.994 1.093m0 3h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                <span className="text-sm font-medium">Tips</span>
              </button>

              {/* Profile Dropdown */}
              <div className="profile-dropdown relative">
                <button 
                  onClick={() => setProfileMenuOpen(!profileMenuOpen)}
                  className="flex items-center space-x-3 px-3 py-2 rounded-lg hover:bg-slate-100 transition-colors"
                >
                  <div className="w-8 h-8 bg-gradient-to-br from-orange-500 to-red-500 rounded-lg flex items-center justify-center">
                    <span className="text-white font-semibold text-sm">{userInitials}</span>
                  </div>
                  <span className="text-sm font-medium text-slate-700 hidden sm:block">{user.name}</span>
                  <ChevronDownIcon className="w-4 h-4 text-slate-400" />
                </button>

                {/* Profile Menu */}
                {profileMenuOpen && (
                  <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-slate-200 py-1 z-50">
                    <button className="w-full text-left px-4 py-2 text-sm text-slate-700 hover:bg-slate-50 flex items-center space-x-2">
                      <UserIcon className="w-4 h-4" />
                      <span>Edit Profile</span>
                    </button>
                    <hr className="my-1 border-slate-200" />
                    <button className="w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50 flex items-center space-x-2">
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" 
                              d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1" />
                      </svg>
                      <span>Sign Out</span>
                    </button>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-4xl mx-auto px-6 py-8">
        {/* Welcome Banner */}
        <div className="bg-gradient-to-r from-orange-500 to-red-500 rounded-2xl p-6 mb-8 text-white">
          <div className="flex items-center space-x-4">
            <div className="w-12 h-12 bg-white/20 rounded-2xl flex items-center justify-center">
              <UserIcon className="w-6 h-6 text-yellow-300" />
            </div>
            <div>
              <h2 className="text-xl font-bold">Welcome, Future Success Story!</h2>
              <p className="text-orange-100 mt-1 leading-relaxed">
                Complete your profile to unlock AI-powered career coaching, personalized job matching, 
                and interview preparation tailored to your unique skills and aspirations.
              </p>
            </div>
          </div>
        </div>

        {/* Registration Form */}
        <div className="bg-white rounded-2xl p-8 shadow-sm border border-slate-200">
          <div className="mb-8">
            <div className="flex items-center space-x-4 mb-2">
              <h3 className="text-lg font-bold text-slate-800">Complete Your Profile</h3>
              <div className="flex-1 h-px bg-gradient-to-r from-slate-300 to-orange-300"></div>
            </div>
            <p className="text-slate-600">Help us understand your career goals and skills to provide personalized recommendations</p>
          </div>

          <form onSubmit={handleSubmit} className="space-y-8">
            {/* Personal Information Section */}
            <div className="space-y-6">
              <h4 className="text-lg font-semibold text-slate-800 flex items-center space-x-2">
                <div className="w-6 h-6 bg-blue-100 rounded-lg flex items-center justify-center">
                  <UserIcon className="w-4 h-4 text-blue-600" />
                </div>
                <span>Personal Information</span>
              </h4>

              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <label className="block text-sm font-semibold text-slate-700">Full Name *</label>
                  <input 
                    type="text"
                    value={seekerForm.name}
                    onChange={(e) => handleInputChange('name', e.target.value)}
                    placeholder="Enter your full name"
                    className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 bg-white"
                    required 
                  />
                </div>

                <div className="space-y-2">
                  <label className="block text-sm font-semibold text-slate-700">Email Address *</label>
                  <input 
                    type="email"
                    value={seekerForm.email}
                    onChange={(e) => handleInputChange('email', e.target.value)}
                    placeholder="your.email@example.com"
                    className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 bg-white"
                    required 
                  />
                </div>
              </div>

              <div className="space-y-2">
                <label className="block text-sm font-semibold text-slate-700">Phone Number *</label>
                <div className="flex flex-col sm:flex-row gap-3">
                  <select
                    value={seekerForm.countryCode}
                    onChange={(e) => handleInputChange('countryCode', e.target.value)}
                    className="w-full sm:w-32 px-3 py-3 border border-slate-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 bg-white"
                  >
                    {countryCodes.map(({ code, country }) => (
                      <option key={code} value={code}>{code} {country}</option>
                    ))}
                  </select>
                  <input 
                    type="tel"
                    value={seekerForm.phone}
                    onChange={(e) => handleInputChange('phone', e.target.value)}
                    placeholder="Your phone number"
                    className="flex-1 px-4 py-3 border border-slate-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 bg-white"
                    required 
                  />
                </div>
              </div>
            </div>

            {/* Professional Information Section */}
            <div className="space-y-6">
              <h4 className="text-lg font-semibold text-slate-800 flex items-center space-x-2">
                <div className="w-6 h-6 bg-emerald-100 rounded-lg flex items-center justify-center">
                  <BriefcaseIcon className="w-4 h-4 text-emerald-600" />
                </div>
                <span>Professional Background</span>
              </h4>

              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <label className="block text-sm font-semibold text-slate-700">Skills *</label>
                  <input 
                    type="text"
                    value={seekerForm.skills}
                    onChange={(e) => handleInputChange('skills', e.target.value)}
                    placeholder="e.g., JavaScript, Project Management, Marketing"
                    className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 bg-white"
                    required 
                  />
                  <p className="text-xs text-slate-500">Separate multiple skills with commas</p>
                </div>

                <div className="space-y-2">
                  <label className="block text-sm font-semibold text-slate-700">Experience *</label>
                  <input 
                    type="text"
                    value={seekerForm.experience}
                    onChange={(e) => handleInputChange('experience', e.target.value)}
                    placeholder="e.g., 3 years as Software Developer"
                    className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 bg-white"
                    required 
                  />
                </div>
              </div>
            </div>

            {/* Help Needed Section */}
            <div className="space-y-6">
              <h4 className="text-lg font-semibold text-slate-800 flex items-center space-x-2">
                <div className="w-6 h-6 bg-purple-100 rounded-lg flex items-center justify-center">
                  <BoltIcon className="w-4 h-4 text-purple-600" />
                </div>
                <span>What help do you need? *</span>
              </h4>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {seekerOptions.map((option) => {
                  const IconComponent = option.icon;
                  return (
                    <label key={option.value} className="flex items-start p-4 border-2 border-slate-200 rounded-xl hover:bg-slate-50 cursor-pointer transition-colors">
                      <input 
                        type="checkbox"
                        checked={seekerForm.seekerDetails.includes(option.value)}
                        onChange={() => handleCheckboxChange(option.value)}
                        className="w-5 h-5 text-orange-600 border-slate-300 rounded focus:ring-orange-500 mt-0.5 mr-4"
                      />
                      <div className="flex-1">
                        <div className="flex items-center space-x-2">
                          <IconComponent className="w-5 h-5 text-orange-600" />
                          <span className="text-sm font-semibold text-slate-900">{option.label}</span>
                        </div>
                        <p className="text-xs text-slate-600 mt-1 leading-relaxed">{option.description}</p>
                      </div>
                    </label>
                  );
                })}
              </div>
            </div>

            {/* Career Goals Section */}
            <div className="space-y-6">
              <h4 className="text-lg font-semibold text-slate-800 flex items-center space-x-2">
                <div className="w-6 h-6 bg-amber-100 rounded-lg flex items-center justify-center">
                  <CheckBadgeIcon className="w-4 h-4 text-amber-600" />
                </div>
                <span>Career Goals</span>
                <span className="text-sm text-slate-500 font-normal">(Optional)</span>
              </h4>

              <div className="space-y-2">
                <textarea 
                  value={seekerForm.goals}
                  onChange={(e) => handleInputChange('goals', e.target.value)}
                  rows="5"
                  placeholder="Tell us about your career aspirations, preferred industries, job types, or any specific goals you'd like to achieve. This helps us provide more personalized recommendations..."
                  className="w-full px-4 py-3 border border-slate-300 rounded-xl focus:ring-2 focus:ring-orange-500 focus:border-orange-500 resize-none bg-white"
                />
              </div>
            </div>

            {/* Submit Section */}
            <div className="border-t border-slate-200 pt-8">
              <div className="flex flex-col sm:flex-row justify-between items-center space-y-4 sm:space-y-0">
                <p className="text-sm text-slate-500">
                  <span className="text-red-500">*</span> Required fields. We'll use this information to provide personalized recommendations.
                </p>
                <button 
                  type="submit" 
                  disabled={isSubmitting || !isFormValid}
                  className="relative px-8 py-3 bg-gradient-to-r from-orange-500 to-red-500 text-white rounded-xl font-semibold text-sm disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-300 flex items-center space-x-2"
                >
                  {isSubmitting ? (
                    <>
                      <svg className="animate-spin w-5 h-5" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                        <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                        <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                      </svg>
                      <span>Submitting...</span>
                    </>
                  ) : (
                    <span>Complete Registration</span>
                  )}
                </button>
              </div>
            </div>
          </form>
        </div>

        {/* Feature Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mt-8">
          <div className="bg-gradient-to-br from-blue-50 to-blue-100 p-6 rounded-xl border border-blue-200">
            <div className="flex flex-col items-center text-center space-y-3">
              <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-blue-600 rounded-xl flex items-center justify-center">
                <BoltIcon className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="font-semibold text-slate-900">AI-Powered Matching</h3>
                <p className="text-xs text-slate-600 mt-1">Get matched with jobs that fit your skills</p>
              </div>
            </div>
          </div>

          <div className="bg-gradient-to-br from-emerald-50 to-emerald-100 p-6 rounded-xl border border-emerald-200">
            <div className="flex flex-col items-center text-center space-y-3">
              <div className="w-12 h-12 bg-gradient-to-br from-emerald-500 to-emerald-600 rounded-xl flex items-center justify-center">
                <AcademicCapIcon className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="font-semibold text-slate-900">Interview Coaching</h3>
                <p className="text-xs text-slate-600 mt-1">Practice with AI interview preparation</p>
              </div>
            </div>
          </div>

          <div className="bg-gradient-to-br from-purple-50 to-purple-100 p-6 rounded-xl border border-purple-200">
            <div className="flex flex-col items-center text-center space-y-3">
              <div className="w-12 h-12 bg-gradient-to-br from-purple-500 to-purple-600 rounded-xl flex items-center justify-center">
                <CheckBadgeIcon className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="font-semibold text-slate-900">Career Guidance</h3>
                <p className="text-xs text-slate-600 mt-1">Receive personalized career advice</p>
              </div>
            </div>
          </div>
        </div>
      </main>

      {/* Tips Modal */}
      {showTipsModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50" onClick={() => setShowTipsModal(false)}>
          <div className="bg-white rounded-2xl max-w-lg w-full max-h-[90vh] overflow-hidden" onClick={(e) => e.stopPropagation()}>
            <div className="flex items-center justify-between p-6 border-b border-slate-200">
              <div>
                <h3 className="text-lg font-bold text-slate-900">Registration Tips</h3>
                <div className="w-12 h-1 bg-gradient-to-r from-orange-500 to-red-500 rounded-full mt-2"></div>
              </div>
              <button
                onClick={() => setShowTipsModal(false)}
                className="p-2 text-slate-400 hover:text-slate-600 rounded-lg hover:bg-slate-100"
              >
                <XMarkIcon className="w-6 h-6" />
              </button>
            </div>
            <div className="p-6 space-y-4">
              <div className="space-y-4">
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 bg-blue-100 rounded-lg flex items-center justify-center">
                    <span className="text-blue-600 font-bold text-xs">1</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900">Be Specific with Skills</h4>
                    <p className="text-sm text-slate-600">List both technical and soft skills. Include specific tools, technologies, or methodologies you're familiar with.</p>
                  </div>
                </div>
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 bg-emerald-100 rounded-lg flex items-center justify-center">
                    <span className="text-emerald-600 font-bold text-xs">2</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900">Describe Your Experience</h4>
                    <p className="text-sm text-slate-600">Include your years of experience and specific roles or projects that showcase your expertise.</p>
                  </div>
                </div>
                <div className="flex items-start space-x-3">
                  <div className="w-6 h-6 bg-purple-100 rounded-lg flex items-center justify-center">
                    <span className="text-purple-600 font-bold text-xs">3</span>
                  </div>
                  <div>
                    <h4 className="font-semibold text-slate-900">Set Clear Goals</h4>
                    <p className="text-sm text-slate-600">The more detailed your career goals, the better we can match you with relevant opportunities and guidance.</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default JobSeekerRegistration;